import { Message } from '@nylas/core';
import { Component, Event, EventEmitter, Host, Method, Prop, State, h } from '@stencil/core';
import { NylasMailboxConnector } from '../../../components';
import { debug, htmlToPlainText } from '@/utils/utils';
import { RegisterComponent } from '@/common/register-component';
import { NylasMailbox } from '../nylas-mailbox/nylas-mailbox';

export type SummarizeEvent = {
  playAudio: (ttsResponseBody: Promise<ReadableStream<Uint8Array> | null>) => Promise<void>;
  updateState: (state: 'loading' | 'playing' | 'stopped') => Promise<void>;
  abortController: AbortController;
  message: Message;
};

@Component({
  tag: 'nylas-summarize-message-button',
  styleUrl: 'nylas-summarize-message-button.scss',
  shadow: true,
})
export class NylasSummarizeMessageButton {
  /**
   * The message to summarize
   */
  @Prop() readonly message?: Message;

  /**
   * The on summarize event handler
   */
  @Event() readonly summarize!: EventEmitter<SummarizeEvent>;

  /**
   * The state of the summarize button
   */
  @State() state: 'loading' | 'playing' | 'stopped' = 'stopped';

  /**
   * The audio element used to play the summary
   */
  private audioRef?: HTMLAudioElement;

  /**
   * The AbortController used to cancel the summarize request
   */
  private abortController: AbortController = new AbortController();

  constructor() {
    this.playAudio = this.playAudio.bind(this);
    this.updateState = this.updateState.bind(this);
  }

  connectedCallback() {}

  disconnectedCallback() {}

  async componentWillLoad() {
    debug(`[nylas-summarize-message-button] Component will load`);
  }

  async componentDidLoad() {
    debug(`[nylas-summarize-message-button] Component did load`);
  }

  /**
   * Handle summarize button click
   * @param event Click event
   */
  private onClick = async (event: Event) => {
    event.preventDefault();
    event.stopPropagation();

    if (this.state === 'stopped') {
      if (this.message) {
        this.abortController = new AbortController();
        const event = this.summarize.emit({
          abortController: this.abortController,
          playAudio: this.playAudio,
          message: this.message,
          updateState: this.updateState,
        });

        if (!event.defaultPrevented) {
          debug(`[nylas-summarize-message-button] Default event not prevented, summarizing message`);

          if (this.audioRef) {
            await this.audioRef
              .play()
              .then(() => this.audioRef?.pause())
              .catch(error => console.error('Error playing audio:', error));
            await this.audioRef.play().catch(error => console.error('Error playing audio:', error));
          }
        }
      }
    } else if (this.state === 'playing') {
      this.abortController?.abort();
      this.audioRef?.pause();
    } else {
      this.state = 'stopped';
    }
  };

  /**
   * Update the state of the summarize button
   * @param state The new state
   * @returns Promise<void>
   */
  @Method()
  public async updateState(state: 'loading' | 'playing' | 'stopped') {
    this.state = state;
  }

  /**
   * Play audio
   * @param ttsResponseBody Text to speech response body
   * @param audioRef Audio element
   */
  @Method()
  public async playAudio(ttsResponseBody: Promise<ReadableStream<Uint8Array> | null>) {
    if (!this.audioRef) {
      this.state = 'stopped';
      return;
    }

    if (!ttsResponseBody) {
      this.state = 'stopped';
      return;
    }

    const audioElement: HTMLAudioElement = this.audioRef;

    // If no MediaSource support, use a blob URL
    if (!('MediaSource' in window)) {
      const rb = await ttsResponseBody;

      if (rb == null) {
        throw new Error('ReadableStream not yet supported in this browser.');
      }

      const reader: ReadableStreamDefaultReader<Uint8Array> = rb.getReader();
      const chunks: Uint8Array[] = [];

      // Start reading chunks
      await this.readChunk(reader, chunks);

      // Create a blob from the chunks
      const blob = new Blob(chunks, { type: 'audio/mpeg' });
      const blobUrl = URL.createObjectURL(blob);
      audioElement.src = blobUrl;
      return;
    }

    const mediaSource = new MediaSource();
    let sourceBuffer: SourceBuffer;

    let sourceDone: boolean = false;

    audioElement.src = URL.createObjectURL(mediaSource);
    mediaSource.addEventListener('sourceopen', onMediaSourceOpen);

    async function onMediaSourceOpen() {
      sourceBuffer = mediaSource.addSourceBuffer('audio/mpeg');
      sourceBuffer.addEventListener('updateend', onUpdateEnd);
      const rb = await ttsResponseBody;

      if (rb == null) {
        throw new Error('ReadableStream not yet supported in this browser.');
      }

      const reader = rb.getReader();
      const pump = () => {
        reader
          .read()
          .then(({ done, value }) => {
            if (done) {
              sourceDone = true;
              return;
            }
            if (!sourceBuffer.updating) {
              sourceBuffer.appendBuffer(value);
            }
            pump();
          })
          .catch(error => {
            console.error('Error while fetching and appending chunks:', error);
          });
      };

      pump();

      if (audioElement.paused) {
        audioElement.play().catch(error => console.error('Error playing audio:', error));
      }
    }

    function onUpdateEnd() {
      if (sourceDone) {
        mediaSource.endOfStream();
        mediaSource.removeEventListener('sourceopen', onMediaSourceOpen);
        mediaSource.removeEventListener('sourceclose', () => {
          debug(`[nylas-summarize-message-button] Source closed`);
        });
        sourceBuffer.removeEventListener('updateend', onUpdateEnd);
        return;
      }
    }
  }

  /**
   * Read chunks from the ReadableStream
   * @param reader The reader to read chunks from for the TTS audio
   * @param chunks The chunks array to append the chunks to
   * @returns Promise<Uint8Array[]>
   */
  private async readChunk(reader: ReadableStreamDefaultReader<Uint8Array>, chunks: Uint8Array[]): Promise<Uint8Array[]> {
    return reader.read().then(({ done, value }) => {
      if (done) {
        // All chunks have been read, return the chunks array
        return chunks;
      }
      chunks.push(value);
      return this.readChunk(reader, chunks); // Read the next chunk
    });
  }

  @RegisterComponent<NylasSummarizeMessageButton, NylasMailboxConnector, Exclude<NylasMailbox['stores'], undefined>>({
    name: 'nylas-summarize-message-button',
    eventToProps: {
      summarize: async (event: CustomEvent<SummarizeEvent>, nylasConnector: NylasMailboxConnector) => {
        const { playAudio, updateState, abortController, message } = event.detail;
        const text = htmlToPlainText(message.body || '');
        await updateState('loading');
        try {
          const summary = await nylasConnector.messages.summarizeText(text);
          const ttsResponseBody = nylasConnector.messages.textToSpeech(summary, abortController);
          await playAudio(ttsResponseBody);
        } catch (error) {
          await updateState('stopped');
          console.error('Error summarizing message:', error);
        }
      },
    },
    fireRegisterEvent: true,
  })
  render() {
    return (
      <Host>
        <sp-theme scale="medium" color="dark">
          <sp-button
            label="Summarize email"
            icon-only
            onClickCapture={this.onClick}
            class={{
              'summarize-button': true,
              'stopped': this.state === 'stopped',
              'loading': this.state === 'loading',
              'playing': this.state === 'playing',
            }}
          >
            {this.state === 'stopped' && <play-icon />}
            {this.state === 'loading' && <loading-icon />}
            {this.state === 'playing' && <stop-icon />}
          </sp-button>
          <audio
            ref={r => (this.audioRef = r)}
            autoPlay={true}
            onPlaying={() => (this.state = 'playing')}
            onPause={() => (this.state = 'stopped')}
            onEnded={() => {
              this.state = 'stopped';
              debug(`[nylas-summarize-message-button] Audio ended`);
            }}
          ></audio>
        </sp-theme>
      </Host>
    );
  }
}
