:host {
  display: block;
}

sp-button {
  padding: 0;
  border: 0;
  border-radius: var(--nylas-border-radius);
  min-block-size: 50%;
  min-inline-size: 2rem;

  background-color: var(--nylas-color-primary-300);
  fill: var(--nylas-color-primary-800);

  &:hover {
    background-color: var(--nylas-color-primary-400);
  }
}

.summarize-button {
  &.stopped {
    animation: none;
  }
  &.playing {
    animation: pulse 2s infinite;
  }
  &.loading {
    display: flex;
    align-content: center;
    height: 1.5rem;
    width: 2rem;
    vertical-align: middle;
    animation: pulse 2s infinite;
    flex-wrap: wrap;
    loading-icon {
      --dot-color: var(--nylas-color-primary-600);
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
    box-shadow: 0 0 0 0 var(--nylas-color-primary-300);
  }
  70% {
    transform: scale(1.05);
    box-shadow: 0 0 0 10px rgba(255, 69, 0, 0);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(255, 69, 0, 0);
  }
}

@keyframes wave {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
